import React, { Component } from 'react';
import {CardBody,CardText} from 'reactstrap';
 class  Card extends Component{
         render(){
        return(
                 this.props.notes.map(note=>{ return( <div className="card border-success mb-3" style={{maxWidth: "20rem"}}>
        
                 <div className="card-body">
                  <h4 className="card-text">{this.props.notes.note}</h4>
                   
                 </div>
          </div>)})
        );
         }
                
               

    
}
export default Card;